<template>
  <transition name="slide" mode="out-in">
    <div class="home">
      <SwiperView />
      <h3>推荐歌单</h3>
      <ul class="personalized">
        <PlayList v-for="item in personalized" :key="item.id" :item="item" />
      </ul>
      <h3>最新音乐</h3>
      <ul class="newPlayList">
        <NewPalyList
          v-for="(item, index) in newPlayList"
          :key="item.id"
          :item="item"
          :index="index"
          @playsongid="playsongid"
          :songId="songId"
          :paused="paused"
        />
      </ul>
    </div>
  </transition>
</template>

<script>
import PlayList from "@/components/PlayList.vue";
import NewPalyList from "@/components/NewPalyList.vue";
import SwiperView from "@/components/SwiperView.vue";

export default {
  components: {
    PlayList,
    NewPalyList,
    SwiperView,
  },
  props: ["songId", "paused"],
  data() {
    return {
      personalized: [],
      newPlayList: [],
    };
  },
  methods: {
    getRecommendPlaylists() {
      this.axios.get("/personalized?limit=6").then((res) => {
        // console.log(res.data.result);
        this.personalized = res.data.result;
      });
    },
    getNewPlaylists() {
      this.axios.get("/personalized/newsong").then((res) => {
        // console.log(res.data.result);
        this.newPlayList = res.data.result;
      });
    },
    playsongid(id, index) {
      this.$emit("playsongid", id, index, this.newPlayList);
    },
  },
  created() {
    this.getRecommendPlaylists();
    this.getNewPlaylists();
  },
};
</script>
<style lang="less" scoped>
.home {
  h3 {
    margin: 0;
    padding-top: 20px;
    position: relative;
    padding-left: 9px;
    margin-bottom: 14px;
    font-size: 17px;
    height: 20px;
    line-height: 20px;
  }
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .personalized {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .newPlayList {
    margin-bottom: 60px;
  }
}

</style>
