<template>
  <transition name="slide" mode="out-in">
    <div
      class="songDetail"
      :style="{ backgroundImage: `url(${songDetail.al.picUrl})` }"
      v-if="songDetail"
    >
      <div class="goBack" ref="nav">
        <div @click="$router.back()">
          <van-icon name="arrow-down" color="#faeaea" size="30" />
        </div>
        <p>私人漫游</p>
        <van-icon
          name="share-o"
          color="#faeaea"
          size="30"
          @click="flag = !flag"
        />
      </div>
      <div class="songPic" @click="showLyric = !showLyric">
        <img src="../assets/image/disc_light.png" class="disc-light" alt="" />
        <img v-if="!showLyric" :src="songDetail.al.picUrl" ref="img" alt="" />
        <!-- 歌词 -->
        <ul
          class="lyric"
          :class="{ play: paused }"
          v-else
          :style="{
            marginTop:
              -1 *
                lyricElementHeight
                  .slice(0, currentLyricIndex + 1)
                  .reduce((sum, item) => sum + item, 0) +
              128 +
              'px',
          }"
        >
          <li
            v-for="(lyr, index) in lyric"
            :key="index"
            :class="{ active: index === currentLyricIndex }"
            ref="lyrs"
          >
            <span
              @click.stop="updateCurrentTime(lyr.time)"
              :style="{
                animationDuration:
                  ((lyric[index + 1]?.time || duration) - lyr.time) * 0.9 + 's',
              }"
              >{{ lyr.text }}</span
            >
          </li>
        </ul>
      </div>
      <div ref="controls">
        <!-- 歌曲详情 歌名,作者 -->
        <div class="controls">
          <div class="songInfo">
            <p class="songName ellipsis">{{ songDetail.name }}</p>
            <p class="songAr ellipsis">
              <span v-for="ar in songDetail.ar" :key="ar.id">{{
                ar.name
              }}</span>
            </p>
          </div>
          <div class="icon">
            <span style="margin-right: 38px"
              ><van-icon name="like-o" size="34" color="faeaea" badge="10w+"
            /></span>
            <span
              ><van-icon
                name="comment-circle-o"
                size="34"
                color="faeaea"
                badge="999+"
            /></span>
          </div>
        </div>
        <!-- 进度条 -->
        <div class="controlsBar" style="">
          <span
            class="bar"
            :style="`width:${(userTime / this.duration) * 100}% ;`"
          ></span>
          <span
            class="sliderTime"
            v-show="inputing"
            :style="`left:${
              (userTime / this.duration < 0.85
                ? `${userTime / this.duration}`
                : '0.85') * 100
            }%`"
            >{{ userTime | timeFormat }}</span
          >
          <input
            type="range"
            v-model.number="userTime"
            min="0"
            :max="duration"
            @input="inputing = true"
            @change="
              inputing = false;
              $emit('changeCurrentTime', userTime);
            "
          />
          <span class="star">{{ currentTime | timeFormat }}</span>
          <span class="end">{{ duration | timeFormat }}</span>
        </div>
        <div class="controlsBtn">
          <div
            style="width: 100%; display: flex; justify-content: space-around"
          >
            <!-- 随机播放 && 循环播放 -->
            <img
              v-show="icon === 0"
              src="../assets/image/round_play.png"
              @click="showImgIcon"
              style="width: 40px; height: 40px; object-fit: contain"
            />
            <img
              v-show="icon === 1"
              src="../assets/image/Shuffle.png"
              @click="showImgIcon"
              style="width: 40px; height: 40px; object-fit: contain"
            />
            <img
              v-show="icon === 2"
              src="../assets/image/Single-tune-circulation.png"
              @click="showImgIcon"
              style="width: 40px; height: 40px; object-fit: contain"
            />
            <!-- 上一首 -->
            <van-icon name="arrow-left" size="40" @click="prevSong(icon)" />
            <!-- 开始 && 暂停 -->
            <div v-show="!paused" @click="playSong">
              <van-icon name="play-circle-o" size="50" />
            </div>
            <div v-show="paused" @click="stopSong">
              <van-icon name="pause-circle-o" size="50" />
            </div>
            <!-- 下一首 -->
            <van-icon name="arrow" size="40" @click="nextSong(icon)" />
            <!-- 列表展示 -->
            <van-icon name="list-switch" size="40" @click="show = !show" />
          </div>
        </div>
      </div>

      <!-- 分享面板 -->
      <shareSheet :flag="flag" @changeFlag="flag = $event" />
      <!-- 列表展示面板 -->
      <div class="waitingPlay" v-if="show" @click="show = !show">
        <div class="waitingPlayList">
          <!-- <div v-if="playlist">暂无歌单</div> -->
          <NewPalyList
            v-for="(item, index) in playlist"
            :key="item.id"
            :item="item"
            :index="index"
            :songId="songId"
            :paused="paused"
            @playsongid="playsongid"
            :currentSongIndex="currentSongIndex"
          />
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import NewPalyList from "@/components/NewPalyList.vue";
import shareSheet from "@/components/shareSheet.vue";

export default {
  props: [
    "paused",
    "currentTime",
    "duration",
    "playlist",
    "songId",
    "currentSongIndex",
    "songDetail"
  ],
  components: {
    shareSheet,
    NewPalyList,
  },
  data() {
    return {
      // 图片旋转角度
      cicle: 0,
      // 定时器
      timer: null,
      // 分享面版显示隐藏
      flag: false,
      // 进度条
      userTime: this.currentTime,
      inputing: false,
      // 歌词 展示
      showLyric: false,
      lyric: "",
      // 列表展示
      show: false,
      // 正在播放的歌曲的id 和 下标
      playingId: 0,
      currentIndex: this.$route.query.currentSongIndex,
      // 切换图标
      icon: 0,
      // 歌词高度
      lyricElementHeight: [],
    };
  },
  computed: {
    currentLyricIndex() {
      return this.lyric.findLastIndex((item) => {
        return item.time <= this.currentTime;
      });
    },
  },
  watch: {
    // 监听数据变化，及时更新props数据
    propValue(newVal) {
      // 处理 prop 的变化
      this.handleNewPropValue(newVal);
    },
    // 监听播放时间，修改进度条
    currentTime(n) {
      if (!this.inputing) {
        this.userTime = n;
      }
      switch (this.icon) {
        case 1:
          if (this.currentTime === this.duration) {
            console.log("随机播放");
            this.nextSong(this.icon);
          }
          break;
        case 2:
          if (this.currentTime === this.duration) {
            console.log("单曲循环");
            this.nextSong(this.icon);
          }
          break;
        default:
          if (this.currentTime === this.duration) {
            this.nextSong(this.icon);
          }
      }
    },
    // 监听歌曲id，确保切换正常
    songId(n) {
      this.playingId = n;
    },
    showLyric(n) {
      if (n) {
        this.$nextTick(() => {
          this.lyricElementHeight = this.$refs.lyrs.map(
            (ele) => ele.offsetHeight
          );
          console.log(this.lyricElementHeight);
        });
      }
    },
    // 判断路由路径获取歌词
    $route(to, from) {
      if (to.path === from.path) {
        this.getSongLyric(); // 假获取歌词数据的方法
      }
    },
  },
  methods: {
    showImgIcon() {
      this.icon = (this.icon + 1) % 3;
    },
    // 获取歌词
    getSongLyric() {
      console.log(this.$route.query.id);

      this.axios.get(`/lyric?id=${this.$route.query.id}`).then((res) => {
        if (res.data && res.data.lrc && res.data.lrc.lyric) {
          this.lyric = res.data.lrc.lyric;
          // 对歌词进行格式转换
          this.lyric = this.lyric
            .split("\n")
            .filter((item) => {
              return item != "";
            })
            .map((item) => {
              var res = item.match(
                /\[(?<m>\d{2}):(?<s>\d{2}\.\d{2,3})\](?<str>.*)/i
              );
              return {
                time: res.groups.m * 60 + parseFloat(res.groups.s),
                text: res.groups.str || "---",
              };
            });
        }
      });
    },
    // 播放歌曲
    playSong() {
      this.$emit("updatePaused", !this.paused);
      this.rotate();
    },
    // 暂停歌曲
    stopSong() {
      this.$emit("updatePaused", !this.paused);
      clearInterval(this.timer);
    },
    // 下一首
    nextSong(n) {
      this.$emit("nextSong", n);
      this.rotate();
    },
    prevSong() {
      this.$emit("prevSong");
    },
    // 旋转图片
    rotate() {
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        if (this.$refs.img) {
          this.$refs.img.style.transform = `rotate(${this.cicle}deg)`;
          this.cicle = this.cicle + 0.5;
        }
      }, 16.6);
    },
    // 传递到歌曲id
    playsongid(id, index) {
      this.currentIndex = index;
      this.$emit("playsongid", id, index);
    },
    // 双击修改歌词播放时间
    updateCurrentTime(time) {
      // console.log(time);
      this.$emit('changeCurrentTime', time);
    },
  },
  activated() {
    console.log("创建了");
    this.getSongLyric();
  },
  created() {
    if (this.$route.query.id) {
      // 获取歌曲id 和下标
      this.playingId = this.$route.query.id;
    }
  },
  mounted() {
    if (this.paused) this.rotate();
  },
  beforeDestroy() {
    // 清除定时器
    this.timer = null;
  },
};
</script>

<style lang="less" scoped>
@keyframes xxx {
  0% {
    background-position-x: 100%;
    background-image: linear-gradient(to right, pink 49%, white 51%);
    color: transparent;
  }
  100% {
    background-position-x: 0%;
    background-image: linear-gradient(to right, pink 49%, white 51%);
    color: transparent;
  }
}
.songDetail {
  width: 100%;
  height: 100vh;
  background-size: contain;
  position: relative;
  z-index: 1;
  color: #faeaea;
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    backdrop-filter: blur(30px);
    filter: brightness(80%);
    z-index: -1;
  }
  .goBack {
    padding: 12px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #faeaea;
    font-size: 18px;
  }
  .songPic {
    text-align: center;
    margin-top: 40px;
    height: 40%;
    overflow: auto;
    .disc-light {
      position: absolute;
      top: 117px;
      left: 67px;
      border: none;
      transform: scale(1.55);
      z-index: -1;
    }
    img {
      width: 240px;
      height: 240px;
      object-fit: contain;
      border-radius: 50%;
    }
    .lyric {
      width: 100%;
      height: 244px;
      transition: all 0.3s;
      &.play {
        li {
          span {
            animation-play-state: running;
          }
        }
      }
      li {
        transition: all 0.3s;
        span {
          line-height: 35px;
          display: inline-block;
          background-size: 200%;
          background-repeat: no-repeat;
          background-position-x: 100%;
          background-clip: text;
        }
      }
      .active {
        span {
          font-size: 20px;
          line-height: 40px;
          // color: #e27676;

          animation: xxx 3s linear;
          animation-play-state: paused;
        }
      }
    }
  }
  .controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 21px 0;
    .songInfo {
      width: 50%;
      overflow: hidden;
      .ellipsis {
        width: 100%;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }
      .songName {
        font-size: 20px;
      }
      .songAr {
        font-size: 16px;
        color: #faf5f5;
      }
      p {
        margin: 8px 0;
      }
    }
    .van-info {
      background: none;
      border: none;
      font-size: 14px;
      margin: 6px 1px 0 0;
    }
  }
  .controlsBar {
    width: 90%;
    border: 2px solid#faeaea;
    border-radius: 4px;
    position: relative;
    margin: 20px auto;
    .bar {
      width: 0%;
      border: 3px solid #e27676;
      border-radius: 4px;
      margin: 0;
      top: -2px;
      left: -3px;
    }
    .sliderTime {
      display: block;
      top: -20px;
      font-size: 12px;
      display: block;
      top: -38px;
      padding: 2px;
    }
    input {
      position: absolute;
      width: 100%;
      box-sizing: border-box;
      margin: 0;
      padding: 0;
      height: 3px;
      top: -2px;
      left: -4px;
      opacity: 0;
    }

    span {
      position: absolute;
      margin-top: 12px;
    }
    .end {
      right: 0;
    }
  }
  .controlsBtn {
    padding-top: 40px;
  }
  .waitingPlay {
    width: 100%;
    height: 100%;
    background: #333333bb;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    .waitingPlayList {
      padding-top: 30px;
      width: 100%;
      height: 60%;
      background-color: #fff;
      border-radius: 30px 30px 0 0;
      position: fixed;
      bottom: 0;
      left: 0;
      overflow: auto;
      .playListItem {
        padding: 16px;
      }
    }
  }
}

.slide-enter-active,
.slide-leave-active {
  transition: transform 0.5s ease, opacity 0.5s ease;
}
.slide-enter {
  transform: translateY(-100%);
  opacity: 1; /* 新视图开始时完全透明 */
}
.slide-leave-to {
  transform: translateY(100%);
  opacity: 0; /* 旧视图结束时完全透明 */
}
</style>