<template>
  <li
    @click="toPlayDetail(item.id, index)"
    :style="songId == item.id ? 'color:red' : 'color:#2C3E50'"
  >
    <div class="songDetail">
      <h4 class="playName">{{ item.name }}</h4>
      <p>
        <span class="tip"></span>
        <span v-for="artist in item.artists" :key="artist.id">{{
          artist.name
        }}</span>
        <span> - {{ item.album.name }}</span>
      </p>
    </div>
    <div v-show="songId !== item.id" class="playBtn"></div>
    <div v-show="songId === item.id" class="playIcon" :class="{ play: paused }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: ["item", "paused", "songId","index"],
  methods: {
    toPlayDetail(id,index) {
      this.$emit("playsongid", id, index);
      this.$root.songId = id;
      this.$router.push({ path: "/playdetail", query: { id: id } });
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes anima {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0);
  }
}
li {
  display: flex;
  align-items: center;
  border-bottom: 0.3px solid #ededed;
  padding: 8px 0;
  justify-content: space-between;
  p {
    margin: 0;
    font-size: 12px;
    color: #888888;
    .tip {
      display: inline-block;
      width: 12px;
      height: 8px;
      margin-right: 4px;
      background: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=)
        no-repeat;
      background-size: 166px 97px;
    }
  }
  .songDetail {
    margin-left: 12px;
    .playName {
      font-size: 17px;
      font-weight: 400;
      margin: 0;
      padding: 8px 0;
    }
  }
  .playBtn {
    width: 22px;
    height: 22px;
    background: url(../assets/image/logo.png) no-repeat;
    background-size: 166px 97px;
    background-position: -24px 0;
    margin-right: 20px;
  }
  .playIcon {
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: space-between;
    margin-right: 20px;
    span {
      display: block;
      width: 15%;
      height: 100%;
      background: #888888;
      animation: anima 0.8s linear infinite;
      transform-origin: bottom;
      animation-play-state: paused;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.4s;
      }
      &:nth-child(3) {
        animation-delay: -0.2s;
      }
      &:nth-child(4) {
        animation-delay: -0.6s;
      }
    }
  }
  .play {
    span {
      animation-play-state: running;
    }
  }
}
</style>