import axios from 'axios';
import Vue from 'vue';
import { Toast } from 'vant';

Vue.use(Toast);

const instance = axios.create({
  // baseURL: 'http://192.168.39.1:3101', // 替换成你的API基础URL 
  baseURL: 'https://apis.netstart.cn/music/', // 替换成你的API基础URL 
  //   timeout: 1000,
});

// 请求拦截器
instance.interceptors.request.use(config => {
  // 在发送请求之前做些什么
  Toast.loading({
    message: '加载中...',
    forbidClick: true,
    overlay:true,
  });
  return config;
}, error => {
  // 对请求错误做些什么
  Toast.clear()
  return Promise.reject(error);
});

// 响应拦截器
instance.interceptors.response.use(response => {
  // 对响应数据做点什么
  Toast.clear()
  return response;
}, error => {
  Toast.fail('请求失败，请稍后再试');
  // 对响应错误做点什么
  return Promise.reject(error);
});

export default instance;