<template>
  <!-- <div class="animate__animated animate__fadeInRight"> -->
  <transition name="slide" mode="out-in">
    <div>
      <input
        type="search"
        v-model="question"
        @keydown.enter="getSong"
        placeholder="搜索歌曲、歌手、专辑"
      />
      <p v-if="hotList" style="padding-left: 10px;">热门搜索</p>
      <ul v-if="hotList" class="hotList">
        <li
          v-for="item in hotList"
          :key="item.id"
          @click="changeQue(item.first)"
        >
          {{ item.first }}
        </li>
      </ul>
      <ul v-if="hotList" class="song">
        <li
          v-for="(item, index) in questionList"
          :key="index"
          style="
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 20px;
          "
        >
          <van-icon name="clock-o" />
          <p class="historyItem" @click="changeQue(item)">{{ item }}</p>
          <p @click="delHistory(index)">X</p>
        </li>
      </ul>

      <p v-if="suggest" style="color: #507daf">搜索“{{ question }}”</p>
      <ul v-if="suggest" class="song">
        <li
          v-for="item in suggest"
          :key="item.id"
          @click="changeQue(item.keyword)"
        >
          {{ item.keyword }}
        </li>
      </ul>

      <p v-if="result">最佳匹配</p>
      <ul v-if="result">
        <!-- <li >{{ item.name }}</li> -->
        <SearchPalyList
          v-for="(item, index) in result"
          :key="item.id"
          :item="item"
          @playsongid="playsongid"
          :paused="paused"
          :songId="songId"
          :index="index"
        />
      </ul>
    </div>
  </transition>
</template>

<script>
import SearchPalyList from "@/components/SearchPalyList.vue";
export default {
  props: ["paused", "songId"],
  components: {
    SearchPalyList,
  },
  data() {
    return {
      question: "",
      result: null,
      hotList: null,
      suggest: null,
      questionList: JSON.parse(localStorage.getItem("questionList")) || [],
    };
  },
  watch: {
    question(n) {
      if (n !== "") {
        this.hotList = null;
        this.result = "";
        this.getSuggest();
      } else {
        this.result = "";
        this.suggest = "";
        this.getHot();
      }
    },
    questionList(n) {
      localStorage.setItem("questionList", JSON.stringify(n));
    },
  },
  methods: {
    getSong() {
      this.axios.get("/search?keywords=" + this.question).then((res) => {
        this.suggest = null;
        this.hotList = null;
        if (!this.questionList.includes(this.question)) {
          this.questionList.push(this.question);
        }
        this.result = res.data.result.songs;
        console.log(this.result);
      });
    },
    getHot() {
      this.axios.get("/search/hot").then((res) => {
        this.hotList = res.data.result.hots;
      });
    },
    getSuggest() {
      this.axios
        .get("/search/suggest?keywords=" + this.question + "&type=mobile")
        .then((res) => {
          this.suggest = res.data.result.allMatch;
        });
    },
    changeQue(song) {
      this.question = song;
      this.$nextTick(() => {
        this.getSong();
      });
    },
    delHistory(id) {
      this.questionList.splice(id, 1);
    },
    playsongid(id, index) {
      this.$emit("playsongid", id, index, this.result);
    },
  },
  created() {
    this.getHot();
    console.log(this.questionList);
  },
};
</script>

<style lang="less" scoped>
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
input {
  width: 92%;
  height: 40px;
  border-radius: 20px;
  padding: 12px;
  border: 0.2px solid #e6e6e6;
  margin: 8px;
}
.hotList {
  display: flex;
  flex-wrap: wrap;
  li {
    margin: 8px;
    padding: 8px 12px;
    border-radius: 20px;
    border: 0.5px solid #e6e6e6;
    color: #333;
    font-size: 14px;
  }
}
.song {
  li {
    padding: 12px;
  }
  .historyItem {
    width: 80%;
    margin: 0;
  }
}

/* 定义过渡的持续时间和效果 */
.slide-enter-active,
.slide-leave-active {
  transition: transform 0.5s ease, opacity 0.5s ease;
}
.slide-enter {
  transform: translateX(100%);
  opacity: 0; /* 新视图开始时完全透明 */
}
.slide-leave-to {
  transform: translateX(-100%);
  opacity: 0; /* 旧视图结束时完全透明 */
}
</style>