<template>
  <li @click="toPlayListDetail(item.id)">
    <div class="thumb">
      <img
        :src="`${item.picUrl}?imageView=1&type=webp&thumbnail=247x0`"
        alt=""
      />
      <span
        ><van-icon name="service-o" color="#fff" size="15" />
        {{ playCount | playCount }}</span
      >
    </div>
    <p>{{ item.name }}</p>
  </li>
</template>

<script>
export default {
  props: ["item"],
  data() {
    return {
      playCount: 0,
    };
  },
  methods: {
    toPlayListDetail(id) {
      this.$router.push({
        path: "/playlistdetail",
        query: {
          id: id,
        },
      });
    },
    countPlayNumber() {
      this.playCount = this.item.playCount;
    },
  },
  created() {
    this.countPlayNumber();
  },
};
</script>

<style lang="less" scoped>
li {
  width: 32%;
  margin-bottom: 8px;

  .thumb {
    position: relative;
    img {
      width: 100%;
    }
    span {
      position: absolute;
      top: 6px;
      right: 8px;
      color: #fff;
    }
  }
  p {
    margin: 0;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    font-size: 14px;
  }
}
</style>