<template>
  <!-- 热门歌单列表详情 -->
  <li
    @click="toPlayDetail(item.id, index)"
    :style="songId == item.id ? 'color:red' : 'color:#2C3E50'"
  >
    <p class="actop">{{ (index + 1).toString().padStart(2, "0") }}</p>
    <div class="songDetail">
      <h4 class="playName">
        {{ item.name }}
        <span class="alia" v-if="item.alia[0]"
          >({{ item.alia[0] || item.alia }})</span
        >
      </h4>
      <p class="artist">
        <span class="tip"></span>
        <span v-for="artist in item.ar" :key="artist.id">
          {{ artist.name }}
        </span>
        <span> - {{ item.al.name }}</span>
      </p>
    </div>
    <div v-show="songId !== item.id" class="playBtn"></div>
    <div v-show="songId === item.id" class="playIcon" :class="{ play: paused }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: ["item", "index", "paused", "songId"],
  methods: {
    // 跳转到页面播放页
    toPlayDetail(id, index) {
      this.$emit("playsongid", id, index);
      this.$router.push({
        path: "/playdetail",
        query: { id: id, currentSongIndex: index },
      });
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes anima {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0);
  }
}
li {
  display: flex;
  align-items: center;
  border-bottom: 0.3px solid #ededed;
  padding: 8px 0 8px 8px;
  justify-content: space-between;
  p {
    margin: 0;
    font-size: 17px;
    color: #888888;
  }
  .songDetail {
    margin-left: 12px;
    width: 80%;
    .playName {
      font-size: 17px;
      font-weight: 600;
      margin: 0;
      padding: 8px 0;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      .alia {
        color: #888888;
        margin-left: 4px;
      }
    }
    .artist {
      font-size: 12px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      .tip {
        display: inline-block;
        width: 12px;
        height: 8px;
        margin-right: 4px;
        background: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28…=)
          no-repeat;
        background-size: 166px 97px;
      }
    }
  }
  .playBtn {
    width: 22px;
    height: 22px;
    background: url(../assets/image/logo.png) no-repeat;
    background-size: 166px 97px;
    background-position: -24px 0;
    margin-right: 20px;
  }
  .playIcon {
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: space-between;
    margin-right: 20px;
    span {
      display: block;
      width: 15%;
      height: 100%;
      background: #888888;
      animation: anima 0.8s linear infinite;
      transform-origin: bottom;
      animation-play-state: paused;
      &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.4s;
      }
      &:nth-child(3) {
        animation-delay: -0.2s;
      }
      &:nth-child(4) {
        animation-delay: -0.6s;
      }
    }
  }
  .play {
    span {
      animation-play-state: running;
    }
  }
}
</style>