<template>
  <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white" :height="145">
    <van-swipe-item v-for="item in bannerList" :key="item.bannerId">
      <img :src="item.pic" alt="">
    </van-swipe-item>
  </van-swipe>
</template>

<script>
export default {
  data(){
    return{
      bannerList:[]
    }
  },
  methods:{
    getBanner(){
      this.axios.get("https://apis.netstart.cn/music/banner?type=2")
      .then(res =>{
        this.bannerList = res.data.banners
      })
    }
  },
  created(){
    this.getBanner()
  }
};
</script>

<style lang="less" scoped>
.my-swipe {
  .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
</style>