<template>
    <transition name="slide" mode="out-in">

  <div>
    <div class="hotTop">
      <div style="padding: 30px 0 30px 30px">
        <div class="title"></div>
        <span class="hottime">更新日期：11月19日</span>
      </div>
    </div>
    <ul>
      <HotPlayList
        v-for="(item, index) in playlist"
        :key="item.id"
        :item="item"
        :index="index"
        @playsongid="playsongid"
        :songId="songId"
        :paused="paused"
      />
    </ul>
  </div>
  </transition>
</template>

<script>
import HotPlayList from "@/components/HotPlayList.vue";
export default {
  props: ["songId", "paused"],
  components: {
    HotPlayList,
  },
  data() {
    return {
      playlist: [],
    };
  },
  methods: {
    getHotPalyList() {
      this.axios.get("/playlist/detail?id=3778678").then((res) => {
        // console.log(res.data.playlist);
        this.playlist = res.data.playlist.tracks;
      });
    },
    playsongid(id, index) {
      this.$emit("playsongid", id, index, this.playlist);
    },
  },
  created() {
    this.getHotPalyList();
  },
};
</script>

