<template>
  <div>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      duration="0.3"
    />
  </div>
</template>

<script>
export default {
  props:["flag"],
  data() {
    return {
      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
    };
  },
  watch:{
    flag(n){
      this.showShare = n
    },
    showShare(n){
        this.$emit('changeFlag',n)
    }
  }
};
</script>

<style lang="less" scoped>
button{
    color: #646566 !important;
}
.van-share-sheet__options{
    justify-content: space-around ;
}
</style>