<template>
  <div id="app">
    <nav v-if="$route.meta.showNav">
      <router-link to="/">推荐歌单</router-link>
      <router-link to="/hot">热歌榜</router-link>
      <router-link to="/search">搜索</router-link>
    </nav>
    <keep-alive exclude="playdetail">
      <router-view
        :songId="songId"
        @playsongid="playsongid"
        :paused="paused"
        @updatePaused="updatePaused"
        :duration="duration"
        :currentTime="currentTime"
        @changeCurrentTime="changeCurrentTime"
        :playlist="playlist"
        :currentSongIndex="currentSongIndex"
        @nextSong="nextSong"
        @prevSong="prevSong"
        :songDetail="songDetail"
      />
    </keep-alive>

    <audio
      :src="songUrl"
      autoplay
      muted
      controls
      ref="audio"
      @durationchange="duration = $refs.audio.duration"
      @timeupdate="currentTime = $refs.audio.currentTime"
    ></audio>

    <div class="playBar" v-if="!songDetail">
      <p>暂无歌曲播放，快播放一曲吧</p>
    </div>
    <div v-if="songDetail && !$route.meta.hidePlayBar" class="playBar">
      <img :src="songDetail.al.picUrl" alt="" @click="toSongPage" />
      <p class="songName">
        {{ songDetail.name }} -
        <span v-for="ar in songDetail.ar" :key="ar.id">{{ ar.name }}</span>
      </p>
      <div style="width: 20%; display: flex; justify-content: space-between">
        <div v-show="!paused" @click="playSong">
          <van-icon name="play-circle-o" size="30" />
        </div>
        <div v-show="paused" @click="stopSong">
          <van-icon name="pause-circle-o" size="30" />
        </div>
        <van-icon name="list-switch" size="30" @click="show = !show" />
      </div>
    </div>
    <div class="waitingPlay" v-if="show" @click="show = !show">
      <div class="waitingPlayList">
        <!-- <div v-if="playlist">暂无歌单</div> -->
        <NewPalyList
          v-for="(item, index) in playlist"
          :key="item.id"
          :item="item"
          :index="index"
          :songId="songId"
          :paused="paused"
          @playsongid="playsongid"
          :currentSongIndex="currentSongIndex"
        />
      </div>
    </div>
  </div>
</template>
<script>
import NewPalyList from "./components/NewPalyList.vue";

export default {
  components: {
    NewPalyList,
  },
  data() {
    return {
      paused: false,
      songUrl: "",
      songDetail: null,
      songId: localStorage.getItem("songId") || 0,
      // 音频时长
      duration: 0,
      currentTime: 0,
      // 播放列表
      playlist: JSON.parse(localStorage.getItem("playlist")) || null,
      show: false,
      // 歌曲下标
      currentSongIndex: localStorage.getItem("currentSongIndex") || 0,
    };
  },
  watch: {
    paused(n) {
      if (n) {
        this.$nextTick(() => {
          this.$refs.audio.play();
        });
      } else {
        this.$nextTick(() => {
          this.$refs.audio.pause();
        });
      }
    },
    currentSongIndex() {
      this.playsongid(this.songId, this.currentSongIndex, this.playlist);
      localStorage.setItem("currentSongIndex", this.currentSongIndex);
    },
    songId() {
      localStorage.setItem("songId", this.songId);
    },
    playlist() {
      localStorage.setItem("playlist", JSON.stringify(this.playlist));
    },
  },
  methods: {
    // 获取歌曲url
    playsongid(id, index, playList) {
      this.songId = id;
      this.currentSongIndex = index;

      this.songUrl = `https://music.163.com/song/media/outer/url?id=${id}.mp3`;
      this.paused = true;
      this.playlist = playList ? playList : this.playlist;
      this.getSongDetail(id);
    },
    //更新播放状态
    updatePaused(paused) {
      this.paused = paused;
    },
    // 获取歌曲详情
    getSongDetail(id) {
      this.axios.get(`/song/detail?ids=${id}`).then((res) => {
        this.songDetail = res.data.songs[0];
        // console.log(res.data.songs);
      });
    },
    // 播放歌曲
    playSong() {
      this.paused = !this.paused;
    },
    // 暂停歌曲
    stopSong() {
      this.paused = !this.paused;
    },
    toSongPage() {
      this.$router.push({
        path: "/playdetail",
        query: {
          id: this.songId,
        },
      });
    },
    changeCurrentTime(time) {
      this.$refs.audio.currentTime = time;
    },
    // 生成随机currentSongIndex
    random() {
      let randomIndex;
      do {
        randomIndex = Math.floor(Math.random() * (this.playlist.length - 1));
      } while (randomIndex === this.currentSongIndex);
      return randomIndex;
    },
    // 下一首
    nextSong(n) {
      switch (n) {
        case 1:
          this.currentSongIndex = this.random();
          console.log(this.currentSongIndex);
          this.songId = this.playlist[this.currentSongIndex].id;
          this.$router.replace({
            path: "/playdetail",
            query: { id: this.songId, currentSongIndex: this.currentSongIndex },
          });
          break;
        case 2:
          this.currentTime = 0;
          this.$refs.audio.play();
          break;
        default:
          if (this.currentSongIndex < this.playlist.length - 1) {
            this.currentSongIndex++;
          } else {
            this.currentSongIndex = 0;
          }
          this.songId = this.playlist[this.currentSongIndex].id;
          this.$router.replace({
            path: "/playdetail",
            query: { id: this.songId, currentSongIndex: this.currentSongIndex },
          });
      }
    },
    // 上一首
    prevSong() {
      if (this.currentSongIndex > 0) {
        this.currentSongIndex--;
      } else {
        this.currentSongIndex = this.playlist.length - 1;
      }
      this.songId = this.playlist[this.currentSongIndex].id;
      this.$router.replace({
        path: "/playdetail",
        query: { id: this.songId, currentSongIndex: this.currentSongIndex },
      });
    },
  },
  created() {
    if (this.songId) {
      this.playsongid(this.songId, this.currentSongIndex, this.playlist);
      this.paused = false;
    }
  },
};
</script>
