<template>
  <!-- <div v-if="detailList" class="animate__animated animate__fadeInRight"> -->
  <transition name="slide" mode="out-in">
    <div v-if="detailList">
      <header :style="{ backgroundImage: `url(${detailList.coverImgUrl})` }">
        <div class="goBack">
          <div @click="$router.back()">
            <van-icon name="arrow-left" color="#faeaea" size="30" />
          </div>
          <p>歌单</p>
          <van-icon name="share-o" color="#faeaea" size="30" />
        </div>
        <div class="plhead_wrap">
          <a :href="detailList.coverImgUrl"
            ><img :src="detailList.coverImgUrl" alt=""
          /></a>
          <div class="playListDetail">
            <h5>{{ detailList.name }}</h5>
            <div class="creator">
              <img :src="detailList.creator.avatarUrl" alt="" />
              <span>{{ detailList.creator.nickname }}</span>
            </div>
          </div>
        </div>
      </header>
      <ul>
        <h3>歌曲列表</h3>
        <HotPlayList
          v-for="(item, index) in detailList.tracks"
          :key="item.id"
          :item="item"
          :index="index"
          @playsongid="playsongid"
          :paused="paused"
          :songId="songId"
        />
      </ul>
      <ul>
        <h3>精彩评论</h3>
        <li v-for="item in commentList" :key="item.id" class="comment">
          <img :src="item.user.avatarUrl" alt="" />
          <div class="main">
            <div style="display: flex; justify-content: space-between">
              <div>
                <p class="nickname">{{ item.user.nickname }}</p>
                <p class="time">{{ item.timeStr }}</p>
              </div>
              <div class="likedCount">
                <span>{{ item.likedCount }}</span>
                <span class="icon"><van-icon name="good-job-o" /></span>
              </div>
            </div>
            <p class="content">{{ item.content }}</p>
          </div>
        </li>
      </ul>
    </div>
  </transition>
</template>

<script>
import HotPlayList from "@/components/HotPlayList.vue";
export default {
  props: ["paused", "songId"],
  components: {
    HotPlayList,
  },
  data() {
    return {
      detailList: null,
      commentList: null,
    };
  },
  methods: {
    getPlayListDetail() {
      this.axios
        .get(`/playlist/detail?id=${this.$route.query.id}`)
        .then((res) => {
          // console.log(res.data.playlist);
          this.detailList = res.data.playlist;
        });
    },
    getPlayListComment() {
      this.axios
        .get(`/comment/hot?id=${this.$route.query.id}&type=2`)
        .then((res) => {
          // console.log(res.data.hotComments);
          this.commentList = res.data.hotComments;
        });
    },
    playsongid(id, index) {
      this.$emit("playsongid", id, index, this.detailList.tracks);
    },
  },
  activated(){
    this.getPlayListDetail();
    this.getPlayListComment();
  },
  created() {
    // this.getPlayListDetail();
    // this.getPlayListComment();
  },
};
</script>

<style lang="less" scoped>
header {
  position: relative;
  padding: 4px 10px 30px 15px;
  overflow: hidden;
  background-size: cover;
  position: relative;
  z-index: 2;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(30px);
    z-index: -1;
  }
  .goBack {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #faeaea;
    font-size: 18px;
  }
  .plhead_wrap {
    display: flex;
    img {
      width: 126px;
      height: 126px;
    }
    .playListDetail {
      width: 60%;
      font-size: 17px;
      padding-left: 12px;
      h5 {
        margin: 0;
        color: #fff;
        font-size: 17px;
      }
      .creator {
        display: flex;
        align-items: center;
        padding: 4px;
        img {
          width: 30px;
          height: 30px;
          border-radius: 50%;
        }
        span {
          color: #fff;
        }
      }
    }
  }
}
ul {
  margin: 0;
  padding: 0;
  margin-bottom: 60px;
  h3 {
    padding-left: 12px;
  }
  .comment {
    display: flex;
    padding: 12px;
    img {
      border-radius: 50%;
      width: 30px;
      height: 30px;
    }
    p {
      margin: 0;
    }
    .main {
      padding-left: 12px;
      width: 90%;
      .nickname {
        font-size: 14px;
        color: #666;
        line-height: 20px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        flex: auto;
      }
      .time {
        font-size: 9px;
        color: #999;
      }
      p {
        color: #333;
        font-size: 15px;
        line-height: 22px;
      }
      .content {
        color: #333;
        font-size: 15px;
        line-height: 22px;
      }
      .likedCount {
        line-height: 22px;
        font-size: 11px;
        color: #999;
        .icon {
          font-size: 18px;
          margin-left: 6px;
        }
      }
    }
  }
}

.slide-enter-active,
.slide-leave-active {
  transition: transform 0.5s ease, opacity 0.5s ease;
}
.slide-enter {
  transform: translateX(100%);
  opacity: 1; /* 新视图开始时完全透明 */
}
.slide-leave-to {
  transform: translateX(-100%);
  opacity: 0; /* 旧视图结束时完全透明 */
}
</style>