import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import HotView from '../views/HotView.vue'
import SearchMusic from '../views/SearchMusic.vue'
import PlayListDetail from '../views/PlayListDetail.vue'
import PlayDetail from '../views/PlayPage.vue'

Vue.use(VueRouter)

const routes = [

  {
    path: '/',
    name: 'home',
    meta: { showNav: true },
    component: HomeView
  },
  {
    path: '/hot',
    name: 'hot',
    meta: { showNav: true },
    component: HotView
  },
  {
    path: '/search',
    name: 'search',
    meta: { showNav: true },
    component: SearchMusic
  },
  {
    path: '/playlistdetail',
    name: 'playlistdetail',
    component: PlayListDetail
  },
  {
    path: '/playdetail',
    name: 'playdetail',
    meta: { hidePlayBar: true },
    component: PlayDetail
  },
]

const router = new VueRouter({
  routes
})

export default router
